@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Oswald&display=swap);
:root {
    --Oswald: 'Oswald', sans-serif;
    --Bebas: 'Bebas Neue', cursive;
}
* {
    box-sizing: border-box;
    scroll-behavior: smooth !important;
}

html {
    overflow-x: hidden !important;
    padding: 0;
    margin: 0;
}

body {
    overflow-x: hidden !important;
    width: 100%;
    height: 100%;
}

/** =================banner====================*/
#banner {
    background-image: url('https://images.unsplash.com/photo-1467232004584-a241de8bcf5d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    color: #ffffff !important;
    position: relative;
}

#banner::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
}

.banner-left,
.banner-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;
}

.banner-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;
}

.banner-right h1 {
    font-size: 5rem;
}

.banner-right h1 img {
    width: 5rem;
}

.banner-left h1 {
    font-weight: bolder;
}

.buttons .btn {
    padding: 10px 35px;
    margin: 50px 20px 0px 0px !important;
}

.buttons .btn-github {
    background-color: bisque;
    color: #000000 !important;
}
.login {
    width: 100%;
    height: 100%;
    background-color: aliceblue;
}
.login-right h1, .signup-right h1 {
    font-weight: bolder;
    padding: 5px 0 15px 0;
    border-bottom: 5px solid #b18800;
}

.login-left h4, .signup-left h4 {
    font-weight: bold;
}


.login-left, .login-right, .signup-left, .signup-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}
.form-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
form label {
    color: #040514 !important;
  }

form input::-webkit-input-placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}

form input:-ms-input-placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}

form input::-ms-input-placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}

form input::placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}
form input {
    border-radius: 4px;
    height: 2rem;
    width: 18rem;
}
nav {
    background: rgb(11, 13, 15) !important;
    font-family: var(--Oswald);
    letter-spacing: 1px;
}
.navbar-brand {
    font-weight: bolder;
    letter-spacing: 2px;
}

.nav-item .btn {
    margin: 0px 25px;
    padding: 5px 25px !important;
    border-radius: 3px;
}

.btn-login {
    background-color: #fff;
    color: #000000 !important;
}

.btn-register {
    background-color: rgb(233, 159, 0);
    color: #000000 !important;
}
.notfound {
    width: 100%;
    height: 100%;
    background-color: rgb(200, 203, 206);
}

.notfound .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}
.notfound .content h1 {
    font-size: 15rem;
}
.notfound .content h2 {
    font-size: 5em;
}

/** =================banner====================*/
.dashboard {
    background-image: url('https://images.pexels.com/photos/2740956/pexels-photo-2740956.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    color: #ffffff !important;
    position: relative;
}

.dashboard::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(8, 22, 53, 0.719);
}

.dashboard .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;

}
.dashboard .content h1, .dashboard .content h3 {
    font-weight: bolder;
}
